.interactive-buttons {
     background: #fff;
     display: flex;
     margin-top: 44px;
}

.btn-action {
     align-items: flex-end;
     background: transparent;
     border: 1px solid #1461f8;
     border-radius: 4px;
     color: #1461f8;
     font: 14px/20px "Segoe UI SemiBold", segoe_ui_semibold, Tahoma, Arial;
     outline: none;
     text-align: center;
}

.btn-action:hover {
     background: rgba(20, 97, 248, 0.25);
}

/* For keyboard focus */
.btn-action:focus-visible {
     box-shadow: 0 0 0 1.5px rgba(0, 123, 255, 0.5);
}

/* For mouse focus */
.btn-action:focus:not(:focus-visible) {
     box-shadow: none;
}

.btn[aria-expanded="true"] {
     background: rgba(20, 97, 248, 0.15);
     color: #1461f8;
}

.btn-img {
     margin: -3.5px 8px 0 0;
}

#display-btn {
     margin: 16px;
}

#capture-btn {
     margin: 16px 16px 16px 0;
}

.showcase-checkbox-container {
     cursor: pointer;
     display: flex;
     font-weight: normal;
     height: auto;
     margin: 0;
     padding: 8px 0;
     position: relative;
}

.close-dropdown {
     cursor: default;
     display: flex;
     height: 28px;
     margin: 1.5px 1.5px 0 0;
}

.showcase-checkbox-container input {
     cursor: pointer;
     opacity: 0;
     position: absolute;
}

.showcase-checkmark {
     visibility: hidden;
}

.showcase-checkbox-container input:checked ~ .showcase-checkmark {
     border-color: #3e65ff;
}

.showcase-checkmark:after {
     content: "";
     display: none;
     position: absolute;
}

.showcase-checkbox-container input:checked ~ .showcase-checkmark:after {
     display: block;
}

.showcase-checkbox-container .showcase-checkmark:after {
     background: #3e65ff;
     border-radius: 50%;
     height: 8px;
     left: 3px;
     top: 3px;
     width: 8px;
}

.checkbox-title {
     display: block;
     margin: 0 24px;
}

#close-btn {
     background: transparent;
     border: 0;
     margin-left: auto;
     outline: none;
     padding: 0;
}

#close-list-btn {
     height: 24px;
     padding: 6px;
     width: 32px;
}

#bookmarks-list {
     background: #fff;
     box-shadow: 0 1.2px 3.6px rgba(0, 0, 0, 0.108),
          0 6.4px 14.4px rgba(90, 61, 61, 0.132);
     height: calc(100vh - 110px);
     margin: 16px 0 0 -16px;
     opacity: 0.98;
     /* Show scroll for small screen */
     overflow-y: auto;
     padding-top: 0;
     position: absolute;
     top: 0;
     transform: translate3d(16px, 50px, 0) !important;
     width: 228px;
}

#bookmarks-list::-webkit-scrollbar-track {
     border-radius: 10px;
     background-color: transparent;
}

#bookmarks-list::-webkit-scrollbar {
     background-color: transparent;
     height: 10px;
     width: 5px;
}

#bookmarks-list::-webkit-scrollbar-thumb {
     background-color: #a0aeb2;
     border-radius: 10px;
     height: 30px;
}

.capture-link-note {
     margin-bottom: 0;
}

.vertical-alignment-helper {
     display: table;
     height: 100%;
     pointer-events: none;
     width: 100%;
}

#tick-icon {
     display: none;
     margin-top: -4px;
}

.vertical-align-center {
     display: table-cell;
     pointer-events: none;
     vertical-align: middle;
}

.modal-content {
